html {
  --moyo-blue: #00acdc;
  background-color: #888;
  margin: 0;
  padding: 0;
  font-family: Roboto, Helvetica, sans-serif;
  font-size: 12px;
}

body {
  margin: 0;
}

.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.logo__image {
  display: block;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  margin-left: 20px;
}

.nav__item:first-child {
  margin: 0;
}

.nav__link {
  color: #000;
  text-transform: uppercase;
  height: 60px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  display: block;
  position: relative;
}

.nav__link.is-active {
  color: var(--moyo-blue);
}

.nav__link.is-active:after {
  content: "";
  background-color: var(--moyo-blue);
  border-radius: 8px;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.nav__item:hover .nav__link {
  color: var(--moyo-blue);
}

